<template>
	<view class="lottery_container">
		<view class="grid_wrap">
			<view class="lottery_wrap">
				<ul class="lottery_grid">
					<li v-for="(item, index) in gridInfo" :class="{ active: current_index == index && index != 8 }" :key="index"
					 @click="luck_draw" :data-index="index">
						<image v-if="index != 8" class="grid_img" mode='aspectFit' :src="item.logo" alt="">
							{{ index == 8 ? '抽奖' : item.text }}
					</li>
				</ul>
			</view>
			<view class="lottery_wrap_border">
				<image :src="imgUrl + '/activity-panel.png'" mode=""></image>
			</view>
		</view>
		<!-- 抽奖完成弹出的窗口 -->
		<!-- <view class="lottery_finish_wrap">
			
		</view> -->
	</view>

</template>

<script>
	import LotteryDraw from './SJ-LotteryDraw.js';
	export default {
		data() {
			return {
				imgUrl: this.config.imgUrl,
				current_index: -1,
			};
		},
		props: {
			gridInfo: {
				type: Array,
				default: []
			},
			drawParam: {
				type: Object,
				default: {}
			},
		},
		onLoad() {
			
		},
		methods: {
			luck_draw(event) {
				let index = event.currentTarget.dataset.index;
				let that = this;
				if (index == 8) {
					// 点击抽奖之后知道获奖位置，修改父组件中drawParam的值
					let drawParam = that.drawParam;
					let win = new LotteryDraw({
							domData: that.gridInfo,
							...drawParam
						},
						function(index, count) {
							that.current_index = index;
							console.log()
							if (drawParam.winingIndex == index && drawParam.totalCount == count) {
								that.$emit('change', that.gridInfo[index])
							}
						}
					);
				}
			}
		}
	};
</script>

<style scoped lang="css">
	@import './SJ-LotteryDraw.css';
	
	.lottery_wrap_border>image {
		width: 100%;
		height: 100%;
	}
</style>
